#about{
        padding: 90px 0px;
        display: flex;
        flex-direction: column;
        /* color: rgb(52, 51, 51); */
} 
#aboutsec{
    display:flex;
    justify-content: space-around;
}
#aboutme{
    font-size:29px;
    
    padding: 5px 0px;
    border-radius: 15px;
 
    margin-bottom: 30px;
}
.paragraph{
   text-align: justify;
    font-size: 20px;
}
.home-img{
    
    height: 380px;
    width: 360px;
    margin: auto;
   
}
#user-detail-intro{
    color: transparent;
}
#aboutsecone{
   
    width:38%;
    text-align: left;
}
#aboutsectwo{
  
    width:30%;
}
@media screen and (min-width:658px) and (max-width:1358px){
    #aboutsec{
        display:flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        /* gap:30px; */
    }
    .home-img{
        border-radius:50% ;
        /* height: 330px;
        width: 280px; */
        margin: auto;
        margin-top: 30px;
       object-fit: cover;
    }
    #aboutsecone{
        
        width:80%;
        text-align: left;
    }
    #aboutme{
        font-size:29px;
        
        text-align: center;
    }
    .paragraph{
        text-align: justify;
        font-size: 24px;
        padding: 0px 10px;
    }
    #summary{
        margin-top: 30px;
    }
    #aboutsectwo{
      
        width:90%;
    }
}

@media screen and (max-width:658px){
    #aboutsec{
        display:flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
       
    }
    #aboutme{
        font-size:27px;
        
        text-align: center;
    }
    .home-img{
        border-radius:50% ;
        /* height: 310px;
        width: 280px; */
        margin: auto;
        margin-top: 30px;
        object-fit: cover;
        /* box-shadow: #4b8118 0px 0px 0px 10px; */
    }
    
    .paragraph{
        text-align: justify;
        font-size: 19px;
        padding: 0px 10px;
    }
    #summary{
        margin-top: 30px;
    }

    #aboutsecone{
    
        width:90%;
        text-align: left;
    }
    #aboutsectwo{
     
        width:90%;
    }
}