#contact-container {
  display: flex;
  font-size: 25px;
  justify-content: space-evenly;
}
.links:hover {
  transform: scale(1.1);
}

@media screen and (min-width: 630px) and (max-width: 1396px) {
  #contact-container {
    display: grid;
    font-size: 25px;
    gap: 30px;
    padding: 0px 25px;
    margin: auto;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media screen and (max-width: 630px) {
  #contact-container {
    display: grid;
    font-size: 25px;
    gap: 30px;
    padding: 0px 35px;
    margin: auto;
    grid-template-columns: repeat(1, 1fr);
  }
}
